$position: 15%;

img {
  position: relative;
  left: $position;
  margin-top:2%;
}
h5{
  position: relative;
  left:14%;
  margin-top:4%;
}
change-login{
  color:white;
}
body{
  background-image: url("https://images.pexels.com/photos/164938/pexels-photo-164938.jpeg?w=1260&h=750&auto=compress&cs=tinysrgb");
  height: 100vh;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
header{
  font-family: 'Inknut Antiqua', serif;
}

header a:hover {
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
  z-index: 1;
}